import { useState, useEffect } from 'react';
import './App.scss';
import TodoList from './components/TodoList';
import AddTodoForm from "./components/AddTodoForm";
import { v4 as uuidv4 } from 'uuid';
import ProgressBar from "./components/ProgressBar";
import { BiRefresh } from "@react-icons/all-files/bi/BiRefresh";
import { BsToggles } from "@react-icons/all-files/bs/BsToggles";
import { BiArchive } from "@react-icons/all-files/bi/BiArchive";
import { MdDeleteForever } from "@react-icons/all-files/md/MdDeleteForever";
import { AiOutlineCloseCircle } from "@react-icons/all-files/ai/AiOutlineCloseCircle";
import { RiMenuAddFill } from "@react-icons/all-files/ri/RiMenuAddFill";

const LOCAL_STORAGE_KEY_CURRENT = 'myTodosApp.todos1.current';
const LOCAL_STORAGE_KEY_CLEARED = 'myTodosApp.todos1.cleared';

function App() {

  const [todos, setTodos] = useState([]);
  const [clearedTodos, setClearedTodos] = useState([]);

  const handleToggleTodo = async (id) => {
    const newTodos = [...todos];
    const todo = newTodos.find(todo => todo.id === id);
    todo.completed = !todo.completed;
    setTodos(newTodos);

  }

  const handleRefreshTodos = async () => {
    let newTodos = [...todos];

    // sort by date
    newTodos.sort((a, b) => ("" + a.endDate).localeCompare(b.endDate));
    
    // sort by completed
    newTodos.sort((a, b) => {
      if (a.completed === false && b.completed === true) {
        return -1;
      }
      if (a.completed === true && b.completed === false) {
        return 1;
      }
      return 0;
    });
    
    setTodos(newTodos);
  }

  const handleToggleEditTodo = (id) => {
    const newTodos = [...todos];
    const todo = newTodos.find(todo => todo.id === id);
    todo.editMode = !todo.editMode;
    setTodos(newTodos);
  }

  const handleAddTodo = (nameInput, descriptionInput, startDateInput, endDateInput, completedInput, editModeInput, viewStateInput) => {
    const newTodo = {
      id: uuidv4(),
      name: nameInput,
      description: descriptionInput,
      startDate: startDateInput,
      endDate: endDateInput,
      completed: completedInput,
      editMode: editModeInput,
      viewState: viewStateInput
    };
    setTodos([newTodo, ...todos]);
  }

  const handleModifyTodo = (id, newPropsObj) => {
    let newTodos = todos.filter(todoObj => todoObj.id !== id);
    let todoToModify = todos.find(todoObj => todoObj.id === id);
    let todoToModifyIdx = todos.findIndex(todoObj => todoObj.id === id);
    newPropsObj = { ...todoToModify, ...newPropsObj };
    newTodos.splice(todoToModifyIdx, 0, newPropsObj);
    setTodos(newTodos);
  }

  const handleClearCompleted = () => {
    if (todos.length < 1) return;
    const clearedTodos = todos.filter(todo => {
      return todo.completed;
    }).map(todo => {
      return { ...todo, ...{ viewState: false }, ...{ editMode: false } };
    });
    setClearedTodos(prev => {
      return [...prev, ...clearedTodos];
    });
    const newTodos = todos.filter(todo => {
      return !todo.completed;
    });
    setTodos(newTodos);
  }

  const handleMarkAllCompleted = () => {
    if (todos.length < 1) return;
    let newTodos;
    if (todos[0].completed === true) {
      newTodos = todos.map(todo => {
        todo.completed = false;
        return todo;
      });
    }
    else {
      newTodos = todos.map(todo => {
        todo.completed = true;
        return todo;
      });
    }
    setTodos(newTodos);
  }

  const handleDeleteClearedTodos = () => {
    if (clearedTodos.length < 1) return;
    setClearedTodos([]);
  }

  const toggleClassOnElement = (element, className) => {
    if (element.classList.contains(className)) {
      element.classList.remove(className);
    }
    else {
      element.classList.add(className);
    }
  }

  useEffect(() => {
    const storedTodos = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY_CURRENT));
    if (storedTodos) setTodos(storedTodos);
    const storedClearedTodos = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY_CLEARED));
    if (storedClearedTodos) setClearedTodos(storedClearedTodos);
  }, []);

  useEffect(() => {
    localStorage.setItem(LOCAL_STORAGE_KEY_CURRENT, JSON.stringify(todos));
  }, [todos]);

  useEffect(() => {
    localStorage.setItem(LOCAL_STORAGE_KEY_CLEARED, JSON.stringify(clearedTodos));
  }, [clearedTodos]);

  const globalFunctions = {
    app: {
      handleToggleTodo: handleToggleTodo,
      handleAddTodo: handleAddTodo,
      handleToggleEditTodo: handleToggleEditTodo,
      handleModifyTodo: handleModifyTodo
    }
  }

  return (
    <div className="App">
      <div className="main-container">
        <div className="todos-container">
          <div className="todos-header">
            <div>
              <span>{todos.filter(t => !t.completed).length}/{todos.length} tasks left</span>
              <ProgressBar details={{ width: "100%", height: ".5rem", fill: `${todos.length < 1 ? "100" : (todos.filter(t => t.completed).length / todos.length) * 100}%`, outerClassName: "progress-bar-outer", innerClassName: "progress-bar-inner" }} />
            </div>
            <h1 className="title-myTodoList">My Task List</h1>
          </div>
          <div className="current-todos">
            {todos.length === 0 && (<h1>...</h1>)}
            <TodoList globalFunctions={globalFunctions} todosArr={todos} />
          </div>
          <div className="toggle-side-view-container open">
            <button className="btn-type-1"
              onClick={() => {
                toggleClassOnElement(document.getElementsByClassName('side-container')[0], "show");
              }} title="add a new item/view archived tasks" >
              <RiMenuAddFill />
              Menu
            </button>
          </div>
          <div className="quick-controls-container">
            <button onClick={handleRefreshTodos} className="btn-type-1" title="Put uncompleted and due tasks at the top">
              <BiRefresh />
              Refresh
            </button>
            <button onClick={handleMarkAllCompleted} className="btn-type-1" title="Mark as as completed or uncompleted">
              <BsToggles />
              Toggle all as completed
            </button>
            <button onClick={handleClearCompleted} className="btn-type-1" title="Put completed tasks in an archive">
              <BiArchive />
              Archive completed tasks
            </button>
          </div>
        </div>
        <div className="side-container">
          <div className="toggle-side-view-container close">
            <button className="btn-type-1"
              onClick={() => {
                toggleClassOnElement(document.getElementsByClassName('side-container')[0], "show");
              }}>
              <AiOutlineCloseCircle />
              Close
            </button>
          </div>
          <div className="controls-container">
            <AddTodoForm globalFunctions={globalFunctions} />
          </div>
          <div className="cleared-todos-container">
            <div className="cleared-todos-header">
              <span>Archived Tasks({clearedTodos.length})</span>
              <button onClick={handleDeleteClearedTodos} className="btn-type-1" title="Delete all tasks in archive">
                <MdDeleteForever />
                Delete all
              </button>
            </div>
            <div className="cleared-todos-wrapper">
              <TodoList todosArr={clearedTodos} />
            </div>
          </div>
        </div>
      </div>
    </div>
  );

}

export default App;